<template>
<div id="app">
    <div class="mainBox">
      <div class="headbox">
        <div class="navbar">
          <router-link to="/shiyan/zuoye">
            <div class="item">作业指导书</div>
          </router-link>
          <router-link to="/shiyan/baojia">
            <div class="item">报价管理</div>
          </router-link>
          <router-link to="/shiyan/moban">
            <div class="item active">报告模板</div>
          </router-link>
          <router-link to="/shiyan/reportGuanli">
            <div class="item">报告管理</div>
          </router-link>
          <router-link to="/shiyan/shiyanren">
            <div class="item">试验人员资料</div>
          </router-link>
        </div>
      </div>
      <div class="bottombox">
          <div class="content2" v-show="isshow">
      <div class="add">
        
        <div class="header">
          <span style="float: left;">新增试验项</span>
          <span style="float: right; cursor: pointer;" @click="show">X</span>
        </div>
        <div
          class="zhong"
          style="margin-top: 50px; margin-left: 100px; text-align: left;  width: 500px;"
        >
          <el-form :model="form">
                <el-form-item label="试验项名称：" :label-width="formLabelWidth" style="margin-top:32px">
                    <el-input v-model="form.name" placeholder="请输入"></el-input>
                </el-form-item>
                <el-form-item label="显示排序：" :label-width="formLabelWidth" style="margin-top:32px">
                    <el-input v-model="form.record" autocomplete="off" placeholder="请输入"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer" style="margin-top:32px">
                <div style="float:left;">
                  <el-button type="info">新增检测项</el-button>
                </div>
                <div style="float:right;">
                  <el-button @click="dialogFormVisible = false">上一步</el-button>
                  <el-button type="primary" @click="dialogFormVisible = false">保存</el-button>
                </div>
            </div>
        </div>
        
      </div>
    </div>
    



    <div class="inner">
      <div class="inner_con">
        <div class="inner_con_header">
          <el-form id="el-form" :inline="true" :model="formInline" class="demo-form-inline" show-header="true">
            <el-form-item label="模板名称：">
              <el-input v-model="formInline.con" placeholder="请输入"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="findResult">查询</el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="clearResult">重置</el-button>
            </el-form-item>
            <el-form-item id="rt-btn">
              <el-button type="success" @click="clearResult"><router-link to='./moban'> +新增</router-link></el-button>
            </el-form-item>
          </el-form>
        </div>
        <div class="inner_con_main">
          <div class="banner" v-bind:key="item.id" v-for="item in mainMessage">
            <div class="banner_top">
              <div class="little_left">
                <h4>主变中性点避雷试验模板</h4>
                <p>
                  <span class="gray">使用设备:</span>
                  <span>{{item.text}}</span>
                </p>
                <p>
                  <span class="gray">编制人:</span>
                  <span>张三</span>
                </p>
                <p>
                  <span class="gray">编制时间：</span>
                  <span>2019-08-23</span>
                </p>
              </div>
              <div class="little_right">
                <ul>
                  <a href="#">
                    <li class="blue" @click="show">新增实验项</li>
                  </a>
                  <a href="#">
                    <li class="blue">修改</li>
                  </a>
                  <a href="#">
                    <li class="red">删除</li>
                  </a>
                </ul>
              </div>
            </div>
            <div class="line"></div>
            <div class="banner_bottom">
              <div class="bottom_left">
                <span class="gray">实验项:</span>
                <span class="close_rt">绕组连同管的绝缘电阻、吸收比</span>
                <p class="bt_test">
                  <span class="close_rt">绕组连同管的绝缘电阻</span>
                </p>
              </div>
              <div class="bottom_right">
                <p class="left_span">
                  <a href="#">
                    <router-link to="./canshuKong"><span class="blue">参数配置</span></router-link>
                  </a>
                  <a href="#">
                    <span class="red">删除</span>
                  </a>
                </p>
                <p class="right_span">
                  <a href="#">
                    <router-link to="./canshuKong"><span class="blue">参数配置</span></router-link>
                  </a>
                  <a href="#">
                    <span class="red">删除</span>
                  </a>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
      </div>
    </div>
  </div>





</template>

<script>
export default {
  data() {
    return {
      isshow: false,
      formInline: {
        con: ""
      },
      dialogTableVisible: false,
                dialogFormVisible: true,
                centerDialogVisible: false,
                form: {
                    name: '',
                    record: '',
                    type: []
                },
                formLabelWidth: '120px',
      mainMessage: [
        {
          id: "01",
          text: "变压器1"
        },
        {
          id: "02",
          text: "变压器2"
        },
        {
          id: "03",
          text: "变压器3"
        }
      ]
    };
  },
  methods: {
    show() {
      this.isshow = !this.isshow;
    },
    findResult() {
      console.log("submit!");
    },
    clearResult() {
      this.formInline.con = "";
    }
  },
   activated() {
    this.$router.push(this.path); //处理默认现实的路由
  },
  beforeRouteLeave(to, from, next) {
    this.path = this.$route.path;
    next();
  }
};
</script>

<style lang="" scoped>
* {
  padding: 0;
  margin: 0;
}

ul,
li {
  list-style: none;
}
a {
  text-decoration: none;
}
span {
  font-size: 14px;
}
#app .banner {
  padding: 8px;
  width: 430px;
  height: 221px;
  margin-left: 19px;
  background: rgba(250, 250, 250, 1);
  float: left;
  /*border: 1px solid #000;*/
}

.inner {
  background: #FFFFFF;
  margin-top: 15px;
  margin-left: 1.25%;
  height: 500px;
  width: 97.5%;
}

.banner_top {
  width: 100%;
  height: 135px;
  /*border: 1px solid #000;*/
}

.little_left {
  height: 100%;
  width: 59%;
  /*border: 1px solid #000;*/
  float: left;
}

.little_left p {
  margin-top: 10px;
}

.little_right {
  height: 100%;
  width: 41%;
  position: relative;
  float: left;
  /*border: 1px solid #000;*/
}

.little_right ul {
  position: absolute;
  right: 0px;
}

.little_right li {
  float: left;
  margin-left: 10px;
}

.banner_bottom {
  width: 100%;
  height: 84px;
  padding-top: 10px;
  /*border: 1px solid #000;*/
}

.bottom_left {
  height: 100%;
  width: 59%;
  float: left;
  position: relative;
  /*border: 1px solid #000;*/
}

.bottom_right {
  position: relative;
  height: 100%;
  width: 41%;
  float: left;
  /*border: 1px solid #000;*/
}

.bottom_right .right_span {
  position: absolute;
  right: 0px;
  top: 30px;
}

.bottom_right .left_span {
  position: absolute;
  right: 0px;
}

.gray {
  color: rgba(102, 102, 102, 1);
}

.blue {
  color: #1890ff;
}

.red {
  color: #ff1818;
}
#app .line {
  height: 1px;
  width: 95%;
  background-color: #dcdcdc;
  margin: auto;
}
.bt_test {
  margin-top: 10px;
}
.close_rt {
  position: absolute;
  left: 56px;
}
.el-button {
    padding: 10px 20px;
  }
.inner_con_header .el-input__inner {
  height: 32px;
  padding: 0;
}
.inner_con_main {
  margin-left: 30px;
}
 .inner_con_header {
   padding: 20px 0;
   margin-left: 50px
 }

 #el-form {
   position: relative;
 }
 #el-form #rt-btn {
   position: absolute;
   right: 32px;  
 }


 .content2 {
  position: absolute;
  width: 100%;
  height: 97%;
  /* background: black; */
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
}

.add {
  position: absolute;
  width: 650px;
  height: 260px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  padding: 15px;
  background: white;
}

.content2 .el-button {
  padding: 3px;
  width: 80px;
  height: 30px;
}

.zhong {
  position: relative
}

.newadd_btm_btn {
  position: absolute;
  left: 0;
}


html,
body {
  width: 100%;
  height: 100%;
}
.mainBox {
  margin-top: 10px;
  width: 97%;
  height: 90%;
  margin-left: 1.5%;
  background: white;
  position: absolute;
  box-shadow: 0px 0px 1px black;
}

.bottombox {
  position: absolute;
  width: 100%;
  height: 100%;
  /* border: 2px solid red; */
}

.navbar {
  width: 100%;
  /* margin-left: 1.5%; */
  height: 30px;

  display: flex;
  /* border: 2px solid red; */
  background: #f0f2f5;
}

.item.active {
  background: #409eff;
  color: white;
}

.item {
  margin-right: 10px;
  width: 85px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  color: black;
  font-size: 14px;
}
</style>



